import './style/main.css'
import * as THREE from 'three'
import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls.js'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js'

import vertex from './shader/vertex.glsl'
import frag from './shader/frag.glsl'
import { Mesh } from 'three'

const gltfLoader = new GLTFLoader()
const dracoLoader = new DRACOLoader()
const textureLoader = new THREE.TextureLoader()
const texture = textureLoader.load('texture03.jpg')
let mixer = null
let model

dracoLoader.setDecoderPath('/draco/')
gltfLoader.setDRACOLoader(dracoLoader)
gltfLoader.load(
    // '/New/glTF/model.gltf',
    'b.glb',
    (gltf)=>{

        const size = 1.3
        gltf.scene.scale.set(size,size,size)
        // gltf.scene.rotation.y = -Math.PI/16
        gltf.scene.position.set(0,-0.1,0)
        scene.add(gltf.scene)
        model = gltf.scene

    // //    Animation
    //    mixer = new THREE.AnimationMixer(gltf.scene)
    //    const action = mixer.clipAction(gltf.animations[0])
    //    action.play()



        gltf.scene.traverse(child=>{
            // console.log(child)
            if(child.isMesh){
                child.material = material
            }
        })

    }
)

/**
 * Sizes
 */
const sizes = {}
sizes.width = window.innerWidth
sizes.height = window.innerHeight


window.addEventListener('resize', () =>
{
    // Save sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()


    renderer.setSize(sizes.width, sizes.height)
})

/**
 * Environnements
 */
// Scene
const scene = new THREE.Scene()

// Camera
const camera = new THREE.PerspectiveCamera(
    75, sizes.width / sizes.height, 0.001, 1000
    )
camera.position.z = 3
scene.add(camera)



// shader
const material = new THREE.ShaderMaterial({ 
    vertexShader:vertex,
    fragmentShader: frag,
    uniforms: {
        uTime: {value:0},
        uTexture: {value: texture}
    },
    side: THREE.DoubleSide
})

material.skinning = true



// Renderer
const renderer = new THREE.WebGLRenderer({
    canvas: document.querySelector('.webgl')
})


renderer.setPixelRatio(window.devicePixelRatio)
renderer.setSize(sizes.width, sizes.height)
// renderer.setClearColor('#323234')
renderer.antialias = true


//target movement
let targetX
let targetY

const windowHalfX = sizes.width /2
const windowHalfY = sizes.height /2

let mouse = {
    x: 0,
    y: 0
}

window.addEventListener('mousemove', (e)=>{
    mouse.x = e.clientX - windowHalfX
    mouse.y = e.clientY - windowHalfY

})


//Controls
const control = new OrbitControls(camera, document.querySelector('.webgl'))
control.update()
control.enableDamping =true
// control.dampingFactor = 1

    
const clock = new THREE.Clock()
let previousTime = 0


/**
 * Loop
 */
const loop = () =>
{
    // Update
    control.update()
    
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - previousTime
    previousTime = elapsedTime
    material.uniforms.uTime.value = elapsedTime

    if(model){
        model.rotation.y += 0.01
    }


    // Model animation
    // if(mixer)
    // {
    //     mixer.update(deltaTime)
    // }



    // //Mouse move
    // targetX = mouse.x * 0.002
    // targetY = mouse.y * 0.001


    // if(model){
    //     // model.rotation.y = -Math.PI/16 + (mouse.x -  0.5) * 0.3
    //     // model.rotation.x = -(mouse.y +  0.5) * 0.3

    //     model.rotation.y += 0.05 * (targetX - model.rotation.y)
    //     model.rotation.x += 0.05 * (targetY - model.rotation.x)

    // }

    // Render
    renderer.render(scene, camera)

    // Keep looping
    window.requestAnimationFrame(loop)
    camera.updateMatrix()
}
loop()